"use client";

import { useRef } from "react";
import { useScroll } from "framer-motion";
import Navbar from "@/components/Navbar";
import ZondaScrollCanvas from "@/components/ZondaScrollCanvas";
import ZondaExperience from "@/components/ZondaExperience";

export default function Home() {
    const containerRef = useRef<HTMLDivElement>(null);

    const { scrollYProgress } = useScroll({
        target: containerRef,
        offset: ["start start", "end end"],
    });

    return (
        <main className="bg-pagani-black text-white relative">
            <Navbar />

            {/* MAIN EXPERIENCE CONTAINER */}
            <section ref={containerRef} className="relative h-[600vh]">
                <div className="sticky top-0 h-screen w-full overflow-hidden">
                    <ZondaScrollCanvas
                        scrollYProgress={scrollYProgress}
                        totalFrames={215}
                        imageFolderPath="/images/zonda-sequence"
                    />
                    <ZondaExperience scrollYProgress={scrollYProgress} />
                </div>
            </section>

            {/* ADDITIONAL SECTIONS */}
            <section className="relative z-20 min-h-screen bg-pagani-black flex items-center justify-center p-20 border-t border-pagani-gold/20">
                <div className="max-w-4xl text-center">
                    <h2 className="text-6xl font-heading mb-10">THE LEGEND CONTINUES</h2>
                    <p className="text-xl opacity-60 leading-loose">
                        The Zonda R is not just a car; it is a statement of intent.
                        A vision of the future crafted with the soul of a pioneer.
                        Horacio Pagani’s masterpiece remains an icon of automotive excellence.
                    </p>
                    <button className="mt-12 px-10 py-4 bg-pagani-gold text-black font-heading tracking-widest hover:bg-bright-gold transition-colors">
                        VIEW TECHNICAL SPECS
                    </button>
                </div>
            </section>

            <footer className="relative z-20 py-20 px-10 border-t border-carbon-gray flex justify-between items-center opacity-40 text-xs">
                <p className="font-heading">© 2026 PAGANI AUTOMOBILI MODENA</p>
                <div className="flex gap-8 font-heading">
                    <a href="#" className="hover:text-pagani-gold">LEGAL</a>
                    <a href="#" className="hover:text-pagani-gold">PRIVACY</a>
                    <a href="#" className="hover:text-pagani-gold">CONTACT</a>
                </div>
            </footer>
        </main>
    );
}
