"use strict";

import { motion, useScroll, useTransform } from "framer-motion";
import { useEffect, useState } from "react";
import { clsx, type ClassValue } from "clsx";
import { twMerge } from "tailwind-merge";

export function cn(...inputs: ClassValue[]) {
    return twMerge(clsx(inputs));
}

export default function Navbar() {
    const { scrollY } = useScroll();
    const [isScrolled, setIsScrolled] = useState(false);

    useEffect(() => {
        return scrollY.onChange((latest) => {
            setIsScrolled(latest > 50);
        });
    }, [scrollY]);

    return (
        <nav
            className={cn(
                "fixed top-0 left-0 right-0 z-50 transition-all duration-300 px-6 py-4 flex justify-between items-center",
                isScrolled ? "glass py-2" : "bg-transparent"
            )}
        >
            <div className="flex items-center gap-2">
                <div className="w-8 h-8 bg-pagani-gold rounded-full flex items-center justify-center text-black font-heading font-bold text-xl">
                    P
                </div>
                <span className="font-heading text-xl tracking-tighter text-pagani-gold">PAGANI</span>
            </div>

            <button className="px-6 py-2 border border-pagani-gold text-pagani-gold font-heading text-sm hover:bg-pagani-gold hover:text-black transition-colors tracking-widest">
                INQUIRE
            </button>
        </nav>
    );
}
