"use client";

import { motion, MotionValue, useTransform } from "framer-motion";
import { carData } from "@/data/carData";

interface Props {
    scrollYProgress: MotionValue<number>;
}

export default function ZondaExperience({ scrollYProgress }: Props) {
    // Hero Opacity & Y
    const heroOpacity = useTransform(scrollYProgress, [0, 0.25, 0.3], [1, 1, 0]);
    const heroY = useTransform(scrollYProgress, [0, 0.3], [0, -50]);

    // Design Opacity & Y
    const designOpacity = useTransform(scrollYProgress, [0.35, 0.45, 0.6, 0.65], [0, 1, 1, 0]);
    const designX = useTransform(scrollYProgress, [0.35, 0.45], [-30, 0]);

    // Engine Opacity & Y
    const engineOpacity = useTransform(scrollYProgress, [0.7, 0.8, 0.95], [0, 1, 1]);
    const engineX = useTransform(scrollYProgress, [0.7, 0.8], [30, 0]);

    return (
        <div className="pointer-events-none sticky top-0 h-screen w-full z-10">

            {/* HERO PHASE (0% - 33%) */}
            <motion.div
                style={{ opacity: heroOpacity, y: heroY }}
                className="absolute inset-0 flex flex-col items-center justify-center p-10"
            >
                <div className="text-center">
                    <motion.h2 className="text-sm font-heading tracking-[0.5em] text-pagani-gold mb-2">PAGANI AUTOMOBILI</motion.h2>
                    <h1 className="text-7xl md:text-9xl font-heading mb-4 text-shadow-gold">{carData.hero.title}</h1>
                    <p className="text-xl md:text-2xl font-body uppercase tracking-widest opacity-60">{carData.hero.tagline}</p>
                </div>

                <div className="absolute bottom-20 left-10 hud-border p-4 bg-black/40 backdrop-blur-md">
                    <p className="text-[10px] font-heading text-pagani-gold">BASE PRICE</p>
                    <p className="text-2xl font-heading">{carData.hero.price}</p>
                </div>
            </motion.div>

            {/* DESIGN PHASE (33% - 66%) */}
            <motion.div
                style={{ opacity: designOpacity, x: designX }}
                className="absolute top-1/2 left-10 md:left-20 -translate-y-1/2 max-w-md"
            >
                <div className="hud-border p-8 bg-black/40 backdrop-blur-lg relative border-l-4 border-l-pagani-gold">
                    <h2 className="text-4xl font-heading mb-4">{carData.design.title}</h2>
                    <p className="text-lg opacity-80 mb-6 leading-relaxed">{carData.design.description}</p>
                    <div className="grid grid-cols-2 gap-4 border-t border-pagani-gold/20 pt-6">
                        {carData.design.specs.map(spec => (
                            <div key={spec.label}>
                                <p className="text-[10px] text-pagani-gold uppercase tracking-tighter">{spec.label}</p>
                                <p className="text-xl font-heading">{spec.value}</p>
                            </div>
                        ))}
                    </div>
                </div>
            </motion.div>

            {/* ENGINE PHASE (66% - 100%) */}
            <motion.div
                style={{ opacity: engineOpacity, x: engineX }}
                className="absolute top-1/2 right-10 md:right-20 -translate-y-1/2 max-w-lg text-right"
            >
                <div className="hud-border p-8 bg-black/40 backdrop-blur-lg relative border-r-4 border-r-pagani-gold">
                    <h2 className="text-4xl font-heading mb-4">{carData.engine.title}</h2>
                    <p className="text-lg opacity-80 mb-6 leading-relaxed">{carData.engine.description}</p>
                    <div className="flex flex-col gap-6 items-end border-t border-pagani-gold/20 pt-6">
                        {carData.engine.specs.map(spec => (
                            <div key={spec.label}>
                                <p className="text-[10px] text-pagani-gold uppercase tracking-tighter">{spec.label}</p>
                                <p className="text-3xl font-heading">{spec.value}</p>
                            </div>
                        ))}
                    </div>
                </div>
            </motion.div>

            {/* FOOTER SCROLL INDICATOR */}
            <div className="absolute bottom-10 left-1/2 -translate-x-1/2 flex flex-col items-center gap-2">
                <div className="w-[1px] h-12 bg-gradient-to-b from-transparent via-pagani-gold to-transparent" />
                <p className="text-[10px] font-heading tracking-[0.3em] opacity-40">SCROLL TO ROTATE</p>
            </div>
        </div>
    );
}
